@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
.btn {
  min-width: 150px;
  border-radius: 50rem;
}

.btn-primary {
  color: #fff;
  background-color: #5810a5;
  border: none;
}

.logo {
  font-family: "Pacifico";
  font-size: 48px;
  text-shadow: 2px 5px rgba(0, 0, 0, 0.5);
}

/* fb button  */

a.fb {
  font-family: Lucida Grande, Helvetica Neue, Helvetica, Arial, sans-serif;
  display: inline-block;
  font-size: 14px;
  padding: 13px 30px 15px 44px;
  background: #3a5a97;
  color: #fff;
  text-shadow: 0 -1px 0 rgba(0, 0, 20, 0.4);
  text-decoration: none;
  line-height: 1;
  position: relative;
  border-radius: 5px;
}
.connect:before {
  display: inline-block;
  position: relative;
  background-image: url(data:image/png;base64,);
  height: 23px;
  background-repeat: no-repeat;
  background-position: 0px 3px;
  text-indent: -9999px;
  text-align: center;
  width: 29px;
  line-height: 23px;
  margin: -8px 7px -7px -30px;
  padding: 2 25px 0 0;
  content: "f";
}
/* fb button  */

.deep-shadow {
  box-shadow: inset 0 -25px 45px #000000, 1px 13px 15px rgba(0, 0, 0, 0.6);
}

#hottest-menu {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.7);
  /* width: 100vw; */
  /* height: 100vh; */
  z-index: 999;
}

#hottest-menu > ul {
  list-style: none;
  /* margin-top: 195px; */
  margin-left: 140px;
}

#hottest-menu > ul > li > h1 {
  color: #fff;
  text-decoration: none;
}

.network {
  display: inline-flex;
  text-decoration: none;
  padding: 1px;
  transition: width 2s;
}
.network:hover {
  padding: 2px;
}

.head {
  background: -webkit-linear-gradient(
    left,
    rgba(88, 16, 165, 1) 0%,
    rgba(26, 139, 206, 1) 50%,
    rgba(61, 70, 183, 1) 100%
  );
  height: 350px;
  margin-bottom: -260px;
}

.logo,
.logo:hover {
  text-decoration: none;
  color: #fff;
}

